<template>
    <div class="song">
        <div class="background">
            <img ref="background" width="100%" height="100%" alt="">
        </div>
        <div @click="back" class="logo">
            <i class="iconfont icon-back"></i>
        </div>
        <audio ref="music" autoplay loop :src="song.url"></audio>
        <div class="wrapper">
            <div class="box">
                <div class="inner">
                    <img ref="img" width="184px" height="184px" class="pic" alt="">
                    <i ref="icon" @click.prevent.stop="play(flag)" :class="'iconfont '+(flag == true ? 'icon-bofang':'icon-iconset0481')"></i>
                </div>
                <p ref="title" class="title"></p>
            </div>
            <div class="lyric">
                <my-scroll class="scroll" ref="scroll">
                    <ul ref="list" class="list">
                        <li ref="item" data-time="item[0]"  v-for="(item,i) in lyric" :key="i">
                            <span :class="{'current': time === item[0]}">{{item[1]}}</span>
                        </li>
                    </ul>
                </my-scroll>
            </div>
        </div>
    </div>
</template>
<script>
    import { getSongUrl,getSong,getLyric } from '@/api/song'
    import myScroll from '@/base/scroll/scroll'
    export default {
        data(){
            return {
                song: [],
                img: [],
                lyric: [],
                flag: true,
                time: 0,
                duration: 0,
                currentNum: 0
            }
        },
        computed:{
            id(){
                return this.$route.query.id
            }
        },
        methods:{
            // async getSongs(id){
            //     this.img = await getSong(id)
            // }
            // bofang 
            play(l){
                if(!l){
                    this.$refs.music.play()
                    this.$refs.img.style.animationPlayState = "running"
                    this.flag = true
                    return 
                }
                this.$refs.music.pause()
                this.flag = false
                this.$refs.img.style.animationPlayState = "paused"
            },
            back(){
                this.$router.back()
            },
            // 提取歌词
            bindLyric(data){
                let reg = /\[(\d{2}):(\d{2})\.\d+\]([^\[\]]+)/g
                let ary = [];
                data.replace(reg,(a,b,c,d) => {
                    let arr = []
                    arr.push(b+":"+c,d)
                    ary.push(arr)
                })
                this.lyric = ary
            },
            zero(n){
                return n < 10 ? "0"+n : n
            },
            // time chuli
            computedTime(time){
                let m = Math.floor(time/60);
                let s = Math.floor(time- m*60)
                return this.zero(m)+":"+this.zero(s)
            },
            addEventListeners() {
                const self = this;
                this.$refs.music.addEventListener('timeupdate', self._currentTime)
                this.$refs.music.addEventListener('canplay', self._durationTime)
            },
            removeEventListeners() {
                const self = this;
                this.$refs.music.removeEventListener('timeupdate', self._currentTime)
                this.$refs.music.removeEventListener('canplay', self._durationTime)
            },
            _currentTime() {
                this.time = this.computedTime(parseInt(this.$refs.music.currentTime))                
            },
            _durationTime() {
                this.duration = parseInt(this.$refs.music.duration)
                console.log(this.duration);
            },
            // 歌词移动
            moveLyric(time){
                this.lyric.forEach((item,index) => {
                    if(item[0] === time){
                        this.currentNum = index 
                    }
                })
                if(this.currentNum > 3){
                    let lineEle = this.$refs.item[this.currentNum - 3]
                    this.$refs.scroll.scrollToElement(lineEle, 1000)
                }else{
                    this.$refs.scroll.scrollTo(0,0,1000)
                }
            }
        },
        created(){
            getSongUrl(this.id).then(res => {
                if(res.data.code === 200){
                    this.song = res.data.data[0]
                }
            })
            getSong(this.id).then(res => {
                if(res.code === 200){
                    this.img = res.songs[0]
                    this.$refs.img.src = this.img.al.picUrl
                    this.$refs.background.src = this.img.al.picUrl
                    this.$refs.title.innerHTML = this.img.name+" - "+this.img.ar[0].name
                }           
            })
            getLyric(this.id).then(res => {
                if(res.data.code === 200){
                    this.lyric = res.data.lrc.lyric
                }
                this.bindLyric(this.lyric)                
            })
        },
        mounted(){
            this.addEventListeners()
        },
        beforeDestroy() {
            this.removeEventListeners()            
        },
        watch: {
            time(){                
                this.moveLyric(this.time)
            }
        },
        components:{
            myScroll
        }
    }
</script>
<style lang="less" scoped>
    @import "../../common/style/variable.less";
    .song{
        position: relative;
        .background {
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            filter: blur(15px)
        }
        .logo {
            position: absolute;
            left: 10px;
            top: 10px;
            .iconfont {
                font-size: @font-size-big;
            }
        }
        .wrapper {
            width: 100%;
            height: 100%;
            padding-top: 70px;
            .box {
                width: 100%;
                height: 565px;
                position: relative;
                .inner {
                    width: 300px;
                    height: 300px;
                    margin: 0 auto;
                    position: relative;
                    &::before{
                        content: "";
                        position: absolute;
                        left: 0;
                        top: 0;
                        right: 0;
                        bottom: 0;
                        background: url('../../common/images/disc.png') no-repeat ;
                        background-size: contain;
                    }
                    .pic {
                        position: absolute;
                        left: 50%;
                        top: 50%;
                        margin: -92px 0 0 -92px;
                        border-radius: 50%;
                        animation: rotate 5s infinite linear;
                    }
                    &::after{
                        content: "";
                        width: 96px;
                        height: 137px;
                        position: absolute;
                        left: 133px;
                        top: -70px;
                        background: url('../../common/images/needle-ip6.png') no-repeat ;
                        background-size: contain;
                        z-index: 5;
                    }
                    .iconfont {
                        position: absolute;
                        left: 50%;
                        margin-top: -30px;
                        margin-left: -30px;
                        top: 50%;
                        color: @color-text-l;
                        font-size: @font-size-super;
                    }
                }
                .title {
                    padding-top: 20px;
                    text-align: center;
                    color: @color-text-l;
                }
            }
            .lyric {
                height: 200px;
                overflow: hidden;
                position: absolute;
                left: 50%;
                margin-left: -80px;
                top: 420px;
                .scroll {
                    height: 100%;
                    overflow: hidden;
                    ul {
                        li {
                            height: 30px;
                            line-height: 30px;
                            font-size: @font-size-xl;
                            span {
                                color: @color-text-l;
                                transition: color 2s;
                                &.current {
                                    color: @color-theme;
                                }
                            }
                            
                        }
                    }
                }
                
            }
        }
    }
    @keyframes rotate {
        0% {
            transform: rotate(0deg)
        }
        100% {
            transform: rotate(360deg)
        }

    }
</style>